body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
    background-color: #282c34;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    min-height: 100vh;
}
.App-body {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  min-height: 100vh;
}

.App-logo {
  height: 30vh;
  margin-top: 15vh;
}

.btn-container{
  -webkit-align-self: center;
          align-self: center;
  margin-top: 5vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  width: 35%;
}

.medium-txt{
  font-size: 24px;
  margin: 0 auto;
  padding-top: 5vh;
  text-align: center;
}

@media (max-width: 599px) and (min-width: 0px) {
  .btn-container{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 20vh;
    margin-top: 2vh;
    width: 80%;
  }
  .medium-txt{
    font-size: 22px;
    width: 80%;
  }
}
.app-page {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: 100vh;
}

.app-article {
    margin-top: 50px;
    margin-left: 5%;
    max-width: 800px;
    width: 90%;
}
.navbar {
    background-color: #181a1f;
    height: 50px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}

.navbar-content {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 100%;
    margin: 0 auto;
    width: 90%;
}

.nav-link {
    color: #ffffff;
    font-size: 1em;
    padding: 0 10px;
    text-decoration: none;
}

.navbar-links {
    height: 100%;
}

.navbar-icon {
    fill: white;
    height: 100%;
}

@media (max-width: 599px) and (min-width: 0px) {
    .nav-link {
        font-size: calc(0.5em + 6px);
    }
}

@media (max-width: 370px) and (min-width: 0px) {
    .nav-link {
        font-size: calc(0.5em + 3px);
    }
}
h1 {
  font-size: 36px;
  -webkit-margin-before: 0m;
          margin-block-start: 0m;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}

p {
  font-size: 17px;
}

@media (max-width: 599px) and (min-width: 0px) {
  p {
    font-size: 16px;
  }
}

